import Link from "next/link";

export default function HomePage() {
    return (
        <main className="relative min-h-screen bg-[#020617] overflow-hidden">
            {/* Background Decorative Elements */}
            <div className="absolute top-[-10%] left-[-10%] w-[40%] h-[40%] bg-indigo-600/20 blur-[120px] rounded-full animate-float" />
            <div className="absolute bottom-[-10%] right-[-10%] w-[40%] h-[40%] bg-purple-600/20 blur-[120px] rounded-full animate-float-slow" />

            {/* Mesh Gradient Overlay */}
            <div className="absolute inset-0 bg-[url('https://grainy-gradients.vercel.app/noise.svg')] opacity-20 pointer-events-none" />

            <div className="container mx-auto px-6 relative z-10">
                <div className="flex flex-col items-center justify-center min-h-[90vh] pt-20 text-center">
                    {/* Floating Badge */}
                    <div className="animate-fade-in-up opacity-0 [animation-fill-mode:forwards]">
                        <span className="inline-flex items-center px-4 py-1.5 rounded-full text-xs font-bold tracking-widest uppercase bg-indigo-500/10 text-indigo-400 border border-indigo-500/20 mb-8 backdrop-blur-md">
                            Next Generation Mobile Tech
                        </span>
                    </div>

                    {/* Main Heading */}
                    <h1 className="text-6xl md:text-8xl font-black text-white mb-8 tracking-tighter animate-fade-in-up delay-100 opacity-0 [animation-fill-mode:forwards]">
                        The Future of <br />
                        <span className="text-gradient">Innovation.</span>
                    </h1>

                    <p className="max-w-xl text-lg md:text-xl text-slate-400 mb-12 animate-fade-in-up delay-200 opacity-0 [animation-fill-mode:forwards] leading-relaxed">
                        Experience precision engineering and breathtaking design. Our newest collection redefines what's possible in the palm of your hand.
                    </p>

                    {/* CTA Section */}
                    <div className="flex flex-col sm:flex-row gap-6 items-center animate-fade-in-up delay-300 opacity-0 [animation-fill-mode:forwards]">
                        <Link
                            href="/products"
                            className="group relative px-10 py-4 bg-white text-slate-950 font-bold rounded-2xl transition-all duration-300 hover:scale-105 active:scale-95 shadow-[0_0_20px_rgba(255,255,255,0.3)] hover:shadow-[0_0_30px_rgba(255,255,255,0.5)]"
                        >
                            Explore Collection
                        </Link>
                        <Link
                            href="/admin"
                            className="px-10 py-4 bg-slate-900/50 text-white font-semibold rounded-2xl border border-white/10 backdrop-blur-md transition-all hover:bg-white/5 hover:border-white/20"
                        >
                            Partner Login
                        </Link>
                    </div>

                    {/* Hero Visual Placeholder (Animated Phone Silhouette) */}
                    <div className="mt-20 w-full max-w-4xl animate-fade-in-up delay-500 opacity-0 [animation-fill-mode:forwards]">
                        <div className="relative aspect-[16/9] w-full bg-gradient-to-t from-indigo-500/20 to-transparent rounded-3xl border border-white/5 overflow-hidden group">
                            <div className="absolute inset-0 bg-mesh opacity-40 mix-blend-overlay" />
                            <div className="absolute inset-0 flex items-center justify-center">
                                <div className="text-white/10 text-[10rem] font-black select-none tracking-tighter">
                                    ULTRA 26
                                </div>
                            </div>
                            {/* Reflection Glow */}
                            <div className="absolute top-[-50%] left-[-50%] w-[200%] h-[200%] bg-[conic-gradient(from_0deg,transparent,rgba(255,255,255,0.1),transparent)] animate-[spin_10s_linear_infinite]" />
                        </div>
                    </div>
                </div>

                {/* Floating Features */}
                <div className="grid grid-cols-1 md:grid-cols-3 gap-8 py-20 animate-fade-in-up delay-500 opacity-0 [animation-fill-mode:forwards]">
                    {[
                        { title: "Bionic Display", desc: "6.9\" OLED with 2000 nits peak brightness." },
                        { title: "Pro Camera", desc: "108MP Cinematic Sensor for unreal detail." },
                        { title: "Titanium build", desc: "Aerospace-grade durability, light as air." }
                    ].map((f, i) => (
                        <div key={i} className="p-8 rounded-3xl bg-white/5 border border-white/10 backdrop-blur-xl hover:bg-white/[0.08] transition-colors group">
                            <h3 className="text-xl font-bold text-white mb-3 group-hover:text-indigo-400 transition-colors">{f.title}</h3>
                            <p className="text-slate-400 text-sm leading-relaxed">{f.desc}</p>
                        </div>
                    ))}
                </div>
            </div>
        </main>
    );
}
